/* === Global Theme Variables === */
:root {
    --primary-blue: #1E3A8A;
    --accent-blue: #3B82F6;
    --background-light: #EFF6FF;
    --text-dark: #1F2937;
    --white: #ffffff;
}

/* === Base Styles === */
body {
    margin: 0;
    background-color: var(--background-light);
    color: var(--text-dark);
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
div,
input,
textarea,
button {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; }

/* === Header Styles === */
.header {
    background: linear-gradient(to right, var(--primary-blue), var(--accent-blue));
    color: var(--white);
    padding: 20px;
    text-align: center;
    border-bottom: 5px solid var(--accent-blue);
}

.name {
    margin: 0;
    font-size: 2.5rem;
}

.contact a {
    color: var(--white);
    text-decoration: none;
}

.contact a:hover {
    text-decoration: underline;
}

/* === Tile Grid Styles === */
.tile-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px;
}

.tile {
    background-color: var(--white);
    border: 2px solid var(--accent-blue);
    border-radius: 10px;
    padding: 20px;
    transition: transform 0.2s, box-shadow 0.2s;
}

.tile:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 16px rgba(59, 130, 246, 0.2);
}

/* === About Section === */
.about-container {
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
    background-color: var(--white);
    border: 1px solid var(--accent-blue);
    border-radius: 12px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.05);
    line-height: 1.6;
}

.about-container h2 {
    font-size: 2rem;
    margin-bottom: 20px;
    color: var(--primary-blue);
}

.about-container p {
    margin-bottom: 16px;
    font-size: 1.1rem;

}

/* === Contact Form Styles === */
.contact-container {
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
    background-color: var(--white);
    border: 1px solid var(--accent-blue);
    border-radius: 12px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.05);
}

.contact-container h2 {
    font-size: 2rem;
    margin-bottom: 20px;
    color: var(--primary-blue);
}

.contact-container form label {
    display: block;
    margin-bottom: 15px;
    font-weight: 500;
}

.contact-container input,
.contact-container textarea {
    width: 95%;
    padding: 10px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 1rem;
    background-color: var(--white);
    color: var(--text-dark);
}

.contact-container button {
    margin-top: 15px;
    background-color: var(--primary-blue);
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    font-size: 1rem;
    cursor: pointer;
}

.contact-container button:hover {
    background-color: var(--primary-blue-dark, #004f9e);
}

/* === Resume Section === */
.section-container {
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
    background-color: var(--white);
    border: 1px solid var(--accent-blue);
    border-radius: 12px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.05);
}

.section-container h2 {
    font-size: 2rem;
    margin-bottom: 16px;
    color: var(--primary-blue);
}

.section-container h3 {
    margin-top: 24px;
    color: #0055aa;
}

.section-container ul {
    padding-left: 20px;
    margin-top: 8px;
}

.section-container li {
    margin-bottom: 6px;
}